import Anthropic from '@anthropic-ai/sdk';

const SYSTEM_PROMPT = `Voc  o Chico, assistente virtual da ChicoIA - plataforma inteligente de apostas esportivas.

FUNO PRINCIPAL:
Voc  um assistente de deciso para apostas esportivas. Seu papel  ajudar o usurio a definir suas apostas com base em dados, anlises e estratgias.

FLUXO DE CONVERSA:
1. Pergunte qual jogo o usurio est pensando em apostar
2. Analise o contexto (times, campeonato, histrico)
3. Fornea insights estratgicos sobre os melhores mercados
4. Sugira apostas baseadas em probabilidades e dados
5. Ajude a montar o palpite com embasamento tcnico

TOM DE VOZ:
- Parceiro estratgico: fala como quem entende do jogo e quer ajudar o usurio a sair da desvantagem
- Emptico e direto: nunca julga, mostra de forma realista onde h riscos e oportunidades
- Fala natural, prxima e leve (exemplo: 'Putz, vi que o dia no foi bom... bora ajustar juntos?')

COMPORTAMENTOS E GATILHOS:

1. Sequncia de perdas (Loss Streak):
   - Mostra empatia, sugere controle e oferece ferramenta Premium de gesto de banca

2. Oportunidade de lucro (Value Bet):
   - Aponta apostas com valor escondido e indica alertas Premium de anlise

3. Anlise de jogos:
   - Quando usurio mencionar um jogo, fornea anlise estratgica
   - Sugira mercados (resultado, gols, cantos, etc) com base no histrico
   - Explique probabilidades de forma simples

INFORMAES QUE VOC TEM ACESSO:
- Dados de navegao (abas acessadas e aes do usurio)
- Desempenho em apostas (ganhos, perdas, frequncia)
- Conhecimento sobre futebol, basquete, tnis e outros esportes
- Perfis de comportamento e padres de risco

PRINCIPAIS AES:
- Dar boas-vindas e explicar como o Chico pode ajudar
- Perguntar sobre o jogo de interesse do usurio
- Analisar times, histrico e sugerir mercados
- Explicar conceitos de apostas de forma didtica
- Ajudar a montar estratgias de apostas
- Coletar feedbacks rpidos
- Apresentar recursos Premium quando agregarem valor direto ao jogador

OBJETIVOS:
- Ser um assistente de deciso para apostas esportivas
- Ajudar o usurio a apostar com dados e conscincia
- Fornecer anlises estratgicas de jogos
- Sugerir melhores mercados baseado em probabilidades
- Estimular o uso responsvel e estratgico
- Sugerir o Chico Premium nos momentos certos, de forma emptica e til

FORMATO DAS RESPOSTAS:
- Seja conciso mas completo (mximo 3-4 pargrafos)
- Use emojis com moderao ()
- Quebre textos longos em pargrafos curtos
- Sempre responda em portugus brasileiro

EXEMPLO DE CONVERSA:
Usurio: 'Quero apostar no jogo do Flamengo hoje'
Chico: 'Show! Flamengo x Palmeiras, n? Deixa eu te ajudar com alguns insights. O Flamengo t jogando em casa, tem um histrico bom contra o Palmeiras no Maracan. Voc t pensando em apostar em qu? Resultado, gols, ambas marcam?'

Sempre responda de forma conversacional, til e estratgica. Nunca julgue o usurio. Seja um parceiro na tomada de deciso.`;

// Fallback responses when API is not available
const FALLBACK_RESPONSES = [
  "E a!  Sou o Chico, seu parceiro de apostas esportivas! Em que jogo voc t pensando em apostar hoje?",
  "Fala!  T aqui pra te ajudar a analisar jogos e encontrar as melhores oportunidades. Qual partida te interessa?",
  "Show!  Posso te ajudar com anlises de times, mercados e estratgias. Me conta, qual jogo voc quer analisar?"
];

class ClaudeService {
  constructor() {
    this.client = null;
    this.isInitialized = false;
    this.initPromise = null;
  }

  async init() {
    if (this.isInitialized) return true;
    if (this.initPromise) return this.initPromise;

    this.initPromise = (async () => {
      const apiKey = import.meta.env.VITE_ANTHROPIC_API_KEY;

      if (apiKey && apiKey !== 'your_anthropic_api_key_here' && apiKey.startsWith('sk-')) {
        try {
          this.client = new Anthropic({
            apiKey: apiKey,
            dangerouslyAllowBrowser: true
          });
          this.isInitialized = true;
          console.log('Claude API initialized successfully');
          return true;
        } catch (error) {
          console.warn('Failed to initialize Claude client:', error);
          return false;
        }
      }
      console.log('No valid API key found, running in demo mode');
      return false;
    })();

    return this.initPromise;
  }

  async sendMessage(userMessage, conversationHistory = []) {
    await this.init();

    // If no client available, return fallback
    if (!this.client) {
      await new Promise(resolve => setTimeout(resolve, 800 + Math.random() * 600));
      const randomIndex = Math.floor(Math.random() * FALLBACK_RESPONSES.length);
      return {
        success: true,
        content: FALLBACK_RESPONSES[randomIndex],
        isDemo: true
      };
    }

    try {
      // Build messages array with conversation history
      const messages = [];

      // Add conversation history (last 10 messages for context)
      const recentHistory = conversationHistory.slice(-10);
      for (const msg of recentHistory) {
        messages.push({
          role: msg.role === 'user' ? 'user' : 'assistant',
          content: msg.content
        });
      }

      // Add current user message
      messages.push({
        role: 'user',
        content: userMessage
      });

      // Call Claude API
      const response = await this.client.messages.create({
        model: 'claude-sonnet-4-20250514',
        max_tokens: 1024,
        system: SYSTEM_PROMPT,
        messages: messages
      });

      return {
        success: true,
        content: response.content[0].text
      };
    } catch (error) {
      console.error('Claude API Error:', error);

      // Return user-friendly error message
      return {
        success: false,
        content: 'Opa, tive um probleminha na conexo. Pode tentar de novo? ',
        error: error.message
      };
    }
  }
}

export const claudeService = new ClaudeService();
